.ig{
	display: grid;
	justify-content: center;
}
.Hangman-word {
	letter-spacing: 1em;
	margin: 0.4em -1em 0.2em 0;
	font-size: 1.5rem;
	color: #07ffc9;
}

.Hangman button {
	border: none;
	font-family: inherit;
	font-size: 1.7rem;
	cursor: pointer;
	width: 10vh;
	margin: 18px 8px 10px 13px;
	background: #07ffc9;
	border-radius: 10px;
	padding-top: 5px;
}

.Hangman-reset {
	color: #d6dfdf;
	box-shadow: 0 6px #07ffc9 !important;
	width: 10rem !important;
	background-color: #07ffc9;
	box-shadow: 0 0 40px #07ffc9;
}
.Hangman button:hover {
	background-color: #fffffa;
}

.Hangman button:disabled {
	color: #d6dfdf;
	background-color: #ff1100;
	border-color: #ff1100;
	box-shadow: 0 0 30px #ff1100;
}
.tx{
	margin: 1em 2em 1em 2em;
}